@import url(https://fonts.googleapis.com/css2?family=Oranienbaum&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@600&display=swap);
:root{
    --thistle: #D8C1D9;
    --gray: rgb(110, 126, 140);
    --offwhite: #F0F1F2;
    --green: #078C7F;
    --brown: #A66A65;
}

body {
  margin: 0;
  font-family: 'Calibri', 'Nunito Sans', sans-serif;
  background-color: #F0F1F2;
  background-color: var(--offwhite);
  font-size: 22px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
    font-family: 'Oranienbaum', serif;
    font-weight: bold;
}


h4 {
    font-family: 'Calibri', sans-serif;
    font-weight: bold;
}


.container {
    background-color: #D8C1D9;
    background-color: var(--thistle);
    color: #078C7F;
    color: var(--green);
    margin: 16vh 0;
}

.card {
    border-radius: 1rem;
}

.About-spinner {
    margin: 10vh 0;
}

.Footer-icon {
    display: inline-block;
    font-size: 30px;
    position: relative;
    margin: 5px;
    color: var(--green);
}

.Footer {
    /* background-color: thistle; */
    color: slategray;
}

.Footer a {
    color: slategray;
}
.Landing {
    height: 100vh;
}

.Landing-container {
    height: 60vh;
    width: 600px;
    position: absolute;
    bottom: -10vh;
    left: 30%;
    padding: 72px 8px;
}

.Landing-header {
    width: 400px;
    position: absolute;
    left: 30%;
}

img {
    width: 300px;
    position: absolute;
    right: 450px;
    top: -13%;
}

img:before {
    content:"";
    background-color: rgb(110, 126, 140, .9);
    position: absolute;
    height: 100%;
    width: 100%;
}

.ProjectsList-container {
    padding-bottom: 6vh;
}
.card-text {
    padding: 1px;
    color: slategray !important;
}

.Project {
    margin: 10vh 0;
}
.Experience-body {
    border: 1px solid teal;
    border-radius: 0;
    margin: 2rem 1rem;
    padding: 1rem;
}
.navbar {
    /* background-color: thistle; */
    color: var(--green);
}

.navbar-brand h1 {
    font-size: 70px;
    color: var(--green);
}
